<template>
    <v-dialog v-model="carrierModifyDialogShow" persistent max-width="500px">
        <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark v-bind="attrs" v-on="on" style="display: none"> Open Dialog </v-btn>
        </template>
        <v-card>
            <v-card-text>
                <v-card-title class="ma-0 pa-0">
                    <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Carrier Modify</span>
                </v-card-title>
                <v-row>
                    <v-col class="pa-1">
                        <v-text-field v-model="partnerInfo.service" dense hide-details label="Service" placeholder="Service" outlined ref="carrierService" readonly disabled></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col class="pa-1">
                        <v-text-field v-model="partnerInfo.name" dense hide-details label="Name" placeholder="Name" outlined ref="carrierName" readonly disabled></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col class="pa-1">
                        <v-text-field type="number" v-model="partnerInfo.weightMin" dense hide-details label="Weight min(Kg)" placeholder="Weight min" outlined ref="carrierWeightMin"></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col class="pa-1">
                        <v-text-field type="number" v-model="partnerInfo.weightMax" dense hide-details label="Weight max(Kg)" placeholder="Weight max" outlined ref="carrierWeightMax"></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col class="pa-1">
                        <v-text-field type="number" v-model="partnerInfo.fuelSurcharge" dense hide-details label="Fuel surcharge(%)" placeholder="Fuel surcharge" outlined ref="carrierFuelSurcharge"></v-text-field>
                    </v-col>
                </v-row>         
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" outlined @click="carrierModifyCancel()">Cancel</v-btn>
                <v-btn color="primary" @click="carrierModifyOk()">Ok</v-btn>
            </v-card-actions>            
        </v-card>
    </v-dialog>
</template>

<script>

export default {
    props: {
            setCarrierModify: {
                type: Object
            }
        },
    data () {
        return {
            partnerInfo: "",
        }
    },
    computed: {
        carrierModifyDialogShow () {
            return this.setCarrierModify.carrierModifyDialogShow;
        },
    },
    mounted() {
    },
    methods: {
        carrierModifyCancel() {
            this.$emit('close');
        },
        carrierModifyOk() {
            console.log(this.partnerInfo)
            if(this.partnerInfo.weightMin == null) {
                return false;
            }
            if(this.partnerInfo.weightMax == null) {
                return false;
            }
            if(this.partnerInfo.fuelSurcharge == null) {
                return false;
            }
            if(this.partnerInfo.weightMin >= this.partnerInfo.weightMax) {
                return false;
            }
            
            this.$parent.setLoadingBar.loadingBarDialogShow = true;

        },
    },
    created() {
        this.$EventBus.$on('carrierModifyPopup', (item) => {
            this.partnerInfo = JSON.parse(JSON.stringify(item));
        })
    },
}
</script>