<template>
  <v-container>
      <v-row class="ma-0 pa-0">
        <!-- 검색조건 -->
        <v-col>
          <v-row style="width:600px">
            <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
              <vue-country-code style="display:none" />
            </v-col>
            <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
              <v-menu v-model="fromDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                <v-text-field v-model="fromDate" label="From" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 125px"></v-text-field>
                </template>
                <v-date-picker v-model="fromDate" @input="fromDateMenu = false"></v-date-picker>
              </v-menu>        
            </v-col>
            <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
              <v-menu v-model="toDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                <v-text-field v-model="toDate" label="To" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 125px"></v-text-field>
                </template>
                <v-date-picker v-model="toDate" @input="toDateMenu = false"></v-date-picker>
              </v-menu>
            </v-col>            
            <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
              <v-text-field hide-details label="Search" placeholder="Search" dense outlined ref="search" style="width: 270px;top:10px"></v-text-field>
            </v-col>
            <v-col class="ma-0 pa-0 flex-grow-0 flex-shrink-0">
              <v-tooltip top color="#9BC298">
                <template v-slot:activator="{ on, attrs }">
                  <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:10px" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/magnify.png" width="40px" />
                </template>
                <span>Search</span>
              </v-tooltip>
            </v-col> 
          </v-row>
        </v-col>
        <!-- 검색조건 -->
      </v-row>
      <v-row>
          <v-col>
              <!-- 리스트 -->
              <v-row>
                  <v-col style="width:0px">
                      <v-data-table :headers="headersPartner" :items="carrierList" v-model="selected" disable-pagination hide-default-footer multi-sort fixed-header item-key="Id" class="elevation-1" height="100%" id="dataTableContent1">
                          <template v-slot:item="props">
                              <tr style="cursor:pointer">
                                  <td align="center" style="height:30px" @click="carrierModify(props.item)">
                                      {{ props.item.service }}
                                  </td>
                                  <td align="center" style="height:30px" @click="carrierModify(props.item)">
                                      {{ props.item.name }}
                                  </td>
                                  <td align="center" style="height:30px" @click="carrierModify(props.item)">
                                      {{ props.item.weightMin }}
                                  </td>
                                  <td align="center" style="height:30px" @click="carrierModify(props.item)">
                                      {{ props.item.weightMax }}
                                  </td>
                                  <td align="center" style="height:30px" @click="carrierModify(props.item)">
                                      {{ props.item.fuelSurcharge }}
                                  </td>
                              </tr>
                          </template>
                      </v-data-table>
                  </v-col>
              </v-row>
              <!-- 로딩바 -->
              <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
              <!-- 로딩바 --> 
              <!-- 캐리어 수정 -->
              <carrierModifyView :setCarrierModify="this.setCarrierModify" @close="carrierModifyCancel"></carrierModifyView>
              <!-- 캐리어 수정 -->
              </v-col>
          </v-row>
  </v-container>
</template>

<script>
import loadingBarView from "@/components/LoadingBarView.vue";
import carrierModifyView from "@/components/CarrierModifyView.vue";
import $ from "jquery";

export default {
  data() {
      return {
          setLoadingBar : { loadingBarDialogShow : false },
          setPartnerRegistration : { partnerRegistrationDialogShow : false },
          setCarrierModify : { carrierModifyDialogShow : false },
          searchText : "",
          headersPartner: [
              { text: "Service", value: "code", width: "200", align: "center" },
              { text: "Name", value: "name", width: "200", align: "center" },
              { text: "Weight min", value: "email", width: "200", align: "center" },
              { text: "Weight max", value: "createdAt", width: "200", align: "center" },
              { text: "Fuel surcharge", value: "createdAt", width: "200", align: "center" },
          ],
          carrierList: [],
          selected : [],
          fromDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
          fromDateMenu: false,
          toDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
          toDateMenu: false,
      }
  },
  components: {
      loadingBarView,carrierModifyView
  },
  methods: {
      keyEnter(value) {
          if (value.code == "Enter") {
              this.getDataInit();
          }
      },
      carrierModify(item) {
          this.setCarrierModify.carrierModifyDialogShow = true;
          this.$EventBus.$emit('carrierModifyPopup', item);
      },
      carrierModifyCancel() {
          this.setCarrierModify.carrierModifyDialogShow = false;
      },
      getDataInit() {
          this.setLoadingBar.loadingBarDialogShow = true;
          this.selected = [];
          $('input[name="item"]').prop('checked', false);
          $('input[name="allCheck"]').prop('checked', false);

          this.carrierList = [
          {
            service : "PANTOS" //hwshwshws
          , name : "PANTOS"
          , weightMin : 30
          , weightMax : 55
          , fuelSurcharge : 2
          }
          ,
          {
            service : "UPS" //hwshwshws
          , name : "UPS"
          , weightMin : 20
          , weightMax : 75
          , fuelSurcharge : 5
          }
          ,
          {
            service : "FEDEX" //hwshwshws
          , name : "FEDEX"
          , weightMin : 50
          , weightMax : 80
          , fuelSurcharge : 5
          }
          ];
          this.setLoadingBar.loadingBarDialogShow = false;
      },
  },
  mounted() {
      if (this.$session.get("loginIdAdmin") == "" || this.$session.get("authorizationAdmin") == "") {
          return false;
      }
      var dataTableHeight = "height:" + String(window.innerHeight - 280) + "px";
      $("#dataTableContent1").children().attr("style", dataTableHeight );  
      $(".v-data-table-header>tr>th").css({'background-color':'#FFC971' });
      $(".v-data-table-header>tr>th").css({'text-align':'center' });
      $(".v-data-table-header>tr>th").css({'font-size':'15px' });
      $(".v-data-table-header>tr>th").css({'font-style':'bold' });

      this.getDataInit();
  },
  updated() {
  },
}
</script>